.price {
  position: relative;
  overflow: hidden;
  padding-top: 147px;
  padding-bottom: 51px;
  background-color: $light-grey;
  background-image: url("../../img/bg-triangle-white.svg");
  background-repeat: no-repeat;
  background-size: 101% auto;
  background-position: 50% 0;
}

@media (min-width: $desktop-width) {
  .price {
    margin-top: -40px;
  }
}
